body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/* Global styles */

a,
a:visited {
  color: #666666;
}

.clear {
  clear: both;
}

td {
  border-top: solid 1px #d3d3d3;
}

td,
th {
  padding: 10px;
}

table {
  border-collapse: collapse;
}

/* Documentation site component styles */
.example {
  border: solid 1px #d3d3d3;
  padding: 16px;
  margin-bottom: 16px;
  background-color: #f4f6f9;
}

.example h4:first-of-type {
  margin-top: 0;
}

.example p:last-of-type {
  margin-bottom: 0;
}

.props tr td:last-of-type {
  text-align: center;
}

.props th,
td {
  text-align: left;
  padding: 5px;
}
#root {
  padding: 0.5em 1em;
}
.navigation {
  float: left;
  width: 250px;
  list-style-type: none;
  padding: 0;
}

.componentpage {
  margin-left: 275px;
}

.textinput {
  margin-bottom: 16px;
}
.textinput__input--state-error {
  border: solid 1px red;
}
.textinput__input-wrapper {
  position: relative;
  display: inline-block;
}
.textinput__error {
  color: red;
}

/* Note how short the selectors can be here compared to plain CSS. With CSS modules, everything
is scoped to the component automatically, so you don't have to spend time
writing long, specific CSS selectors like we do with BEM. - they'll only apply
to the component where this css is imported. :)
*/
.TextInput_error__2YVSd {
  color: red;
}

.TextInput_inputError__2WDV- {
  border: solid 1px red;
}

.TextInput_fieldset__3nRGQ {
  margin-bottom: 16px;
}
.TextInput_wrapper__3unUI {
  position: "relative";
  display: "inline-block";
}

